<template>
  <section>
    <div class="row">
      <div class="col-12 col-md-7">
        <h6 class="text-red">Total Rewards Distributed:</h6>
        <div class="card">
          <div class="div-scroll">
            <template v-for="(token, i) in tokens">
              <div class="mr-4" :key="i">
                <p>
                  <strong>{{ token.symbol }}</strong>
                  <span>{{ token.balance }}</span>
                </p>
              </div>
            </template>
          </div>
        </div>
      </div>
      <div class="col-12 col-md-5">
        <div class="box-right mt-5">
          <h5>Donate gas to expedite rewards!</h5>
          <small>Max: .02 BNB</small> <br />
          <input type="number" v-model="gas" />
          <button
            type="button"
            class="btn btn-outline-danger"
            @click.prevent="donate"
            :disabled="!getUserAccount || isDonating"
          >
            Donate Gas
          </button>
        </div>
      </div>
      <div class="col-12 mt-5 mb-4">
        <h6 class="text-blue">My Rewards:</h6>
        <div class="card border-blue">
          <div class="div-scroll">
            <table class="table table-borderless">
              <thead>
                <tr>
                  <th width="100" scope="col">Token</th>
                  <th scope="col">Calimed</th>
                  <th scope="col">Owed</th>
                  <th width="100" scope="col"></th>
                </tr>
              </thead>
              <tbody>
                <template v-for="(token, i) in tokens">
                  <tr :key="i">
                    <td>{{ token.symbol }}</td>
                    <td>{{ token.claimed }}</td>
                    <td>{{ token.owed }}</td>
                    <td>
                      <button
                        type="button"
                        class="btn btn-outline-danger btn-claim"
                        @click.prevent="claim(token.symbol)"
                        :disabled="!getUserAccount || isLoading"
                      >
                        Claim
                      </button>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Dashboard",
  data() {
    return {
      gas: 0.2,
      isLoading: false,
      isDonating: false,
      tokens: {
        XRP: {
          symbol: "XRP",
          balance: "0",
          claimed: "0",
          owed: "0",
        },
        AVAX: {
          symbol: "AVAX",
          balance: "0",
          claimed: "0",
          owed: "0",
        },
        BTC: {
          symbol: "BTC",
          balance: "0",
          claimed: "0",
          owed: "0",
        },
        CASH: {
          symbol: "CASH",
          balance: "0",
          claimed: "0",
          owed: "0",
        },
        ADA: {
          symbol: "ADA",
          balance: "0",
          claimed: "0",
          owed: "0",
        },
      },
    };
  },

  mounted() {
    this.readValue();
    this.timer = setInterval(() => {
      this.readValue();
    }, 10000);
  },
  methods: {
    readValue() {
      if (this.getWeb3) {
        Promise.all([
          this.getTrackerInstance.methods
            .totalDividendsDistributed(this.getXRPAddress)
            .call(),
          this.getTrackerInstance.methods
            .totalDividendsDistributed(this.getAVAXAddress)
            .call(),
          this.getTrackerInstance.methods
            .totalDividendsDistributed(this.getBTCAddress)
            .call(),
          this.getTrackerInstance.methods
            .totalDividendsDistributed(this.getCASHAddress)
            .call(),
          this.getTrackerInstance.methods
            .totalDividendsDistributed(this.getADAAddress)
            .call(),
          this.getTrackerInstance.methods
            .accumulativeDividendOfToken(
              this.getUserAccount,
              this.getXRPAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .accumulativeDividendOfToken(
              this.getUserAccount,
              this.getAVAXAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .accumulativeDividendOfToken(
              this.getUserAccount,
              this.getBTCAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .accumulativeDividendOfToken(
              this.getUserAccount,
              this.getCASHAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .accumulativeDividendOfToken(
              this.getUserAccount,
              this.getADAAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .withdrawableDividendOfToken(
              this.getUserAccount,
              this.getXRPAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .withdrawableDividendOfToken(
              this.getUserAccount,
              this.getAVAXAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .withdrawableDividendOfToken(
              this.getUserAccount,
              this.getBTCAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .withdrawableDividendOfToken(
              this.getUserAccount,
              this.getCASHAddress
            )
            .call(),
          this.getTrackerInstance.methods
            .withdrawableDividendOfToken(
              this.getUserAccount,
              this.getADAAddress
            )
            .call(),
        ]).then(
          ([
            XRPBalance,
            AVAXBalance,
            BTCBalance,
            CASHBalance,
            ADABalance,
            XRPDividend,
            AVAXDividend,
            BTCDividend,
            CASHDividend,
            ADADividend,
            XRPOwed,
            AVAXOwed,
            BTCOwed,
            CASHOwed,
            ADAOwed,
          ]) => {
            this.tokens.XRP.balance = this.humanized(XRPBalance, 3);
            this.tokens.AVAX.balance = this.humanized(AVAXBalance, 3);
            this.tokens.BTC.balance = this.humanized(BTCBalance, 3);
            this.tokens.CASH.balance = this.humanized(CASHBalance, 3);
            this.tokens.ADA.balance = this.humanized(ADABalance, 3);
            this.tokens.XRP.claimed = this.humanized(XRPDividend, 3);
            this.tokens.AVAX.claimed = this.humanized(AVAXDividend, 3);
            this.tokens.BTC.claimed = this.humanized(BTCDividend, 3);
            this.tokens.CASH.claimed = this.humanized(CASHDividend, 3);
            this.tokens.ADA.claimed = this.humanized(ADADividend, 3);
            this.tokens.XRP.owed = this.humanized(XRPOwed, 3);
            this.tokens.AVAX.owed = this.humanized(AVAXOwed, 3);
            this.tokens.BTC.owed = this.humanized(BTCOwed, 3);
            this.tokens.CASH.owed = this.humanized(CASHOwed, 3);
            this.tokens.ADA.owed = this.humanized(ADAOwed, 3);
          }
        );
      }
    },
    humanized(number, fix) {
      return Number(
        this.getWeb3.utils.fromWei(number.toString(), "ether")
      ).toFixed(number == 0 ? 3 : fix);
    },
    donate() {
      let amount = (this.gas * 1e18).toString();
      this.isDonating = true;
      this.getHedgeInstance.methods
        .processDividendTracker(amount)
        .send({
          from: this.getUserAccount,
        })
        .on("transactionHash", (hash) => {
          console.log("Transaction hash", hash);
          this.$toasted.show("Transaction submitted to the network");
        })
        .on("receipt", (receipt) => {
          console.log("Receipt: ", receipt);
          this.isDonating = false;
          this.$toasted.show("Transaction completed successfully");
        })
        .on("error", (error) => {
          this.isDonating = false;
          console.log("Error receipt: ", error);
        });
    },
    claim(symbol) {
      let addr = this["get" + symbol + "Address"];
      this.isLoading = true;
      this.getHedgeInstance.methods
        .withdrawOldDividends(addr)
        .send({
          from: this.getUserAccount,
        })
        .on("transactionHash", (hash) => {
          console.log("Transaction hash", hash);
          this.$toasted.show("Transaction submitted to the network");
        })
        .on("receipt", (receipt) => {
          console.log("Receipt: ", receipt);
          this.isLoading = false;
          this.$toasted.show("Transaction completed successfully");
        })
        .on("error", (error) => {
          this.isLoading = false;
          console.log("Error receipt: ", error);
        });
    },
  },
  computed: {
    ...mapGetters("wallet", [
      "getWeb3",
      "getUserAccount",
      "getXRPAddress",
      "getAVAXAddress",
      "getBTCAddress",
      "getCASHAddress",
      "getADAAddress",
      "getHedgeInstance",
      "getTrackerInstance",
      "getXRPInstance",
      "getAVAXInstance",
      "getBTCInstance",
      "getCASHInstance",
      "getADAInstance",
    ]),
  },
  watch: {
    getADAInstance() {
      this.readValue();
    },
  },
};
</script>